
$breakpoints: (
  mobile-sm: 20em,        // 320px - 
  mobile: 30em,           // 480px - 
  tablet-sm: 48em,        // 768px - 
  tablet: 64em,           // 1024px - 
  laptop: 80em,           // 1280px - 
  desktop: 90em           // 1440px - 
);


// :
// @include mq(tablet-sm) {
//   column-count: 2;
// }

// _mixins.scss

@mixin mq($size) {
  $value: map-get($breakpoints, $size);

  @if $value {
    @media (min-width: $value) {
      @content;
    }
  } @else {
    @warn "No breakpoint defined for `#{$size}`";
  }
}
